@font-face {
  font-family: "PixelFont";
  src: url(./fonts/3573.eot) format('eot'),
  url(./fonts/3573.woff) format('woff'),
  url(./fonts/3573.woff2) format('woff2');
}


.cell {
  display: flex;
  padding: 0;
  font-family: 'PixelFont';
  box-sizing: border-box;
  border: 2px solid black;
  font-size: 70px;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  outline: none;
}

/* .xo-value {
